            <!-- Text -->
            <h5 class="video-analysis" id="text" style="width: 100%">
              DASALAN AT TOCSOHAN
            </h5>
            <span>
              <router-link to="/marcelo-del-pilar">
                <span class="author-color">Marcelo H. Del Pilar</span>
              </router-link>
            </span>

            <h6 class="h6-top-padding">Ang Tanda</h6>
            <span>
              Ang tanda nang cara-i-cruz ang ipangadya mo sa amin Panginoon
              naming Fraile sa manga bangkay naming, sa ngalan nang Salapi at
              nang Maputing binte, at nang Espiritung Bugaw. Siya naua.
            </span>

            <h6 class="h6-top-padding">Pagsisisi</h6>
            <span
              >Panginoon kong Fraile, Dios na hindi totoo at labis nang pagkatuo
              gumaga at sumalacay sa akin: pinagsisihan kong masakit sa tanang
              loobang dilang pag-asa lo sa iyo, ikaw nga ang berdugo ko.
              Panginoon ko at kaauay ko na inihihibic kong lalo sa lahat,
              nagtitica akong matibay na matibay na dina muli-muling mabubuyo sa
              iyo: at lalayuan ko na at pangingilagan ang balanang makababacla
              nang loob ko sa pag-asa sa iyo, macalilibat nang dating sakit nang
              manga bulsa ko, at nagtitica naman acong maglalathala nang dilang
              pagcadaya ko umaasa akong babambuhin ka rin, alang-alang sa mahal
              na panyion at pangangalakal mo nang Cruz, sa pagulol sa akin. Siya
              naua.;
            </span>

            <h6 class="h6-top-padding">Ang Amain Namin</h6>
            <span
              >Amain naming sumasaconvento ka, sumpain ang ngalan mo, malayo sa
              amin ang kasakiman mo, quitlin ang liig mo ditto sa lupa para nang
              sa langit. Saulan mo cami ngayon nang aming kaning iyonh
              inaraoarao at patauanin mo kami sa iyong pagungal para nang pag
              papataua mo kung kami nacucualtahan; at huag mo kaming ipahintulot
              sa iyong manunukso at iadya mo kami sa masama mong dila.
            </span>

            <h6 class="h6-top-padding">Ang Aba Guinoong Baria</h6>
            <span>
              Aba guinoong Baria nakapupuno ka nang alcancia ang Fraile'I
              sumasainyo bukod ka niyang pinagpala't pina higuit sa lahat,
              pinagpala naman ang kaban mong mapasok. Santa Baria Ina nand
              Deretsos, ipanalangin mo kaming huag anitan ngayon at cami
              ipapatay. Siya naua.
            </span>

            <h6 class="h6-top-padding">Ang Aba Po Santa Baria</h6>
            <span>
              Aba po Santa Bariang Hari, inagao nang Fraile, ikao ang kabuhayan
              at katamisan. Aba bunga nang aming pauis, ikaw ang pinagpaguran
              naming pinapanaw na tauong Anac ni Eva, ikaw nga ang
              ipinagbubuntonh hininga naming sa aming pagtangis dito sa bayang
              pinakahapishapis. Ay aba pinakahanaphanap naming para sa aming
              manga anak, ilingon mo sa aming ang cara- i cruz mo man lamang at
              saka bago matapos ang pagpanaw mo sa amin ay iparinig mo sa amin
              ang iyong kalasing Santa Baria ina nang deretsos, malakas at
              maalam, matunog na guinto kami ipanalangin mong huag magpatuloy sa
              aming ang manga banta nang Fraile. Amen. Ang Manga Utos Nang
              Fraile Ang manga utos nang Fraile ay sampo: Ang nauna: Sambahin mo
              ang Fraile na lalo sa lahat. Ang ikalaua: Huag kang mag papahamak
              manuba nang ngalang deretsos. Ang ikatlo: Mangilin ka sa Fraile
              lingo man at fiesta. Ang ikapat: Isangla mo ang catauan mo sa
              pagpapalibing sa ama't ina, Ang ikalima: Huag kang mamamatay kung
              uala pang salaping pang libing. Ang ikanim: Huag kang makiapid sa
              kanyang asaua. Ang ikapito: Huag kang makinakaw. Anh ikaualo: Huag
              mo silang pagbibintangan, kahit ka masinungalingan. Ang ikasiyam:
              Huag mong ipagkait ang iyong asaua. Ang ikapulo: Huag mong itangui
              ang iyong ari. Itong sampong utos nang Fraile'I dalaua ang
              kinaoouian. Ang isa: Sambahin mo ang Fraile lalo sa lahat. Ang
              ikalaua: Ihayin mo naman sa kaniya ang puri mo't kayamanan. Siya
              naua. Ang manga kabohongang asal, ang pangala'i tontogales ay
              tatlo. Igalang mo Katakutan mo Ang Fraile At Pag Manuhan mo..
            </span>

            <!-- Context - Introduction -->
            <h5 class="video-analysis" id="analysis">
              Introduction to the Text
            </h5>
            <span>
              Satire is very common, especially in the Philippines where it is
              used as a means to cope, bring about awareness about social issues
              and injustices, or even just plain humor. However, satire did not
              simply pop into existence in recent times! In fact, this
              particular text by Marcelo H. del Pilar was written during the
              colonial occupation of the Spaniards here in the Philippines.
              According to Galang-Parena (2019.): “Remembering is always
              accompanied by conflict as people have unique ways of retaining an
              event and what it means. When memory becomes part of the public
              sphere, it is inevitably steeped in controversy. The publicness of
              memory is constituted most of all in the field of popular or mass
              culture—mediated through the channels of mass communication—and it
              is here especially that we can locate a privileged site for the
              playing out of the ethical issues arising from the historical or
              the remembered past.” While the aforementioned text focuses on
              Willie Nepomuceno’s art, it is still true for del Pilar’s text,
              where it brings to light the wrongdoings of the Spanish friars,
              which frequently show paradoxical behavior in contrast to their
              teachings (Salcedo, 2014).
              <br /><br />
              However, this text was not simply used by del Pilar only; many
              other versions of it exist to mock politicians (Salcedo, 2014)
              such as former President Gloria Arroyo and even former President
              Benigno Aquino (texts appended below).
            </span>

            <!-- Context - Analysis -->
            <h5 class="video-analysis" style="width: 100%">
              Analysis of the Text
            </h5>

            <h6 class="h6-top-padding">Ang Tanda</h6>
            <span>
              This section of the text speaks to how the friars abused their
              power and position to get what they wanted. It satirizes the Sign
              of the Cross, explicitly showing to the reader that instead of
              these friars (who supposedly are the administrators and
              authorities driven by religious fervor), are instead acting on
              their lust and greed, given that the ones they worship are the
              Salapi (money), Maputing binte (white thighs/women), and that they
              are possessed by the Espiritung Bugaw (pimp spirit).
            </span>
            <h6 class="h6-top-padding">Pagsisisi</h6>
            <span>
              In line with Ang Tanda, this section deals with the friars acting
              like a god. Parodying the Act of Contrition, the Filipinos are to
              ‘worship’ the friars that are untrue, unjust and even abusive
              (despite their religious affiliation). Similarly, in contemporary
              times, this is slowly becoming the norm in political landscapes,
              with many various politicians acting like a god, or even the
              general public treating them as such.
            </span>
            <h6 class="h6-top-padding">Ang Amain Namin</h6>
            <span>
              Building on the parody of the Act of Contrition, this prayer
              satirizes the Lord’s Prayer. The section primarily deals with the
              Filipinos hatred or being against the friars or Spanish
              imperialism, which highlights the problems they have caused.
              Unlike the previous sections which were more allegorical, this
              text is a more implicit call to action.
            </span>
            <h6 class="h6-top-padding">
              Ang Aba Guinoong Barya & Ang Aba Po Santa Barya
            </h6>
            <span>
              This section satirizes the Hail Mary, personalizing the concept of
              money into a person and that person being taken away by the
              friars.
            </span>
            <h6 class="h6-top-padding">
              Ang Manga Utos Nang Fraile
            </h6>
            <span>
              Finally, this text is a parody of the ten commandments.
            </span>
            <br />
            <br />
            <span>
              ….every deed, sacrifice and giving no longer goes to God and the
              Spanish Friars seize it. (Salcedo, 2014)
            </span>

            <!-- Context - Analysis -->
            <h5 class="video-analysis">Relevance to Mikey Bustos</h5>
            <span>
              Marcelo H. del Pilar wrote the Dasalan and Tocsohan because of his
              experiences with the friars. To an extent,
              <router-link to="/mikey-bustos">
                <span class="author-color">Mikey Bustos</span>
              </router-link>
              acts as a FOIL instead; rather than using his medium (Youtube
              videos, in this case) to satirically mock the corruption or
              destruction that has been done to the Philippines, he tries to use
              his skits to educate or to produce humorous effect linked
              Philippine culture, as is the case with his Filipino National
              Heroes Video, condensing history into a catchy, upbeat rap, and
              his various ‘tutorials’ to Filipino traditions, like the Filipino
              Christmas Tutorial, or even humorously jabbing at the Filipino
              Accent, where it recently won 1st sexiest accent in Asia.
            </span>

